<template>
	<view style="padding:40rpx 32rpx 0;">
		<view style="text-align: center;font-size: 36rpx;color: #fff;margin-bottom: 24rpx;font-weight: bold;">邀请记录
		</view>
		<view class="view_box">
			<view :style="transitionStyle">
				<template v-for="item in list">
					<view class="card_box">
						<image class="card_box_img" src="/static/doudou.png" mode=""></image>
						<text class="card_box_text">180*****4503</text>
						<view class="card_box_coupon">30元优惠券</view>
					</view>
				</template>
			</view>
		</view>
		<button style="margin-top: 100rpx;" @click="toPage('/pages/introduction/introduction-v2')">打开空白模版-vue2</button>
		<button style="margin-top: 20rpx;" @click="toPage('/pages/introduction/introduction-v3')">打开空白模版-vue3</button>
	</view>
</template>

<script setup>
	import {
		onLoad,
		onHide,
		onShow
	} from '@dcloudio/uni-app'
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from "vue";
	let transitionStyle = ref('')
	const timr = ref(null)
	const list = ref([1, 2, 3, 4, 5, 6, 7, 8, 9, 1, 2, 3, 4, 5, 6, 7, 8, 9])
	const instance = getCurrentInstance();
	onShow(() => {
		init()
	})
	//删除定时器
	onHide(() => {
		transitionStyle.value = ''
		clearInterval(timr.value)
		timr.value = null
	})
	const init = () => {
		// 主要通过 transition 的过度时间 控制 滚动速度
		setTimeout(() => {
			transitionStyle.value = 'transition: all 8s linear 0s;transform: translateY(-50%);'
			if(timr.value) return
			timr.value = setInterval(() => {
				transitionStyle.value = ''
				setTimeout(() => {
					transitionStyle.value =
						'transition: all 8s linear 0s;transform: translateY(-50%);'
				}, 20)
			}, 8000)
		}, 20)
	}
	const toPage = (page) => {
		uni.navigateTo({
			url: page
		})
	}
</script>

<style lang="scss">
	page {
		background-color: #aaa2fb;
	}

	.view_box {
		border-radius: 24rpx;
		background-color: #fff;
		height: 400rpx;
		overflow: hidden;
	}

	.card_box {
		display: flex;
		align-items: center;
		padding: 12rpx 24rpx;
		box-sizing: border-box;
	}

	.card_box_img {
		width: 80rpx;
		height: 80rpx;
		margin-right: 24rpx;
		border-radius: 50%;
		overflow: hidden;
	}

	.card_box_text {
		flex: 1;
		font-size: 30rpx;
		color: #333;
	}

	.card_box_coupon {
		background-color: #dcd7ff;
		font-size: 28rpx;
		color: #8c89c5;
		border-radius: 8rpx;
		padding: 8rpx 24rpx;
	}
</style>
